html {
  height: 100%;
}
body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
}
input:focus-visible {
  outline: none;
}
.vissually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.section-title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
.field {
  padding: 16px 0;
  border: none;
  border-bottom: 2px solid #000000;
  background-color: inherit;
}
.field::placeholder {
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  opacity: 0.3;
}
.field:hover {
  border-color: #63d1bb;
}
.field:hover + .field-icon {
  fill: #63d1bb;
  opacity: 1;
}
.field:active,
.field:focus {
  border-color: #63d1bb;
}
.field:active + .field-icon,
.field:focus + .field-icon {
  fill: #63d1bb;
  opacity: 1;
}
.field-icon {
  fill: #000000;
  opacity: 0.2;
}
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 10;
}
.modal-container--close {
  display: none;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff``");
  font-display: swap;
}
.button {
  width: 100%;
  display: block;
  padding: 14px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  box-sizing: border-box;
  border: 2px solid #444444;
  text-align: center;
  color: #444444;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #ffffff;
  cursor: pointer;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
  border-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
  border-color: #444444;
}
.button--add-cart {
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
}
@media (min-width: 768px) {
