  align-items: center;
}
.copyright__text {
  margin-right: 27px;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #000000;
}
.copyright__link {
  display: flex;
}
.copyright__icon {
  fill: #63d1bb;
}
.copyright__icon:hover {
  fill: #56C3B2;
}
.copyright__icon:active {
  fill: #56C3B2;
  opacity: 0.3;
}
.main-footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .main-footer__logo {
    display: block;
  }
  .main-footer__logo-image {
    fill: #000000;
  }
}
.products {
  padding: 30px;
  box-sizing: border-box;
}
.products__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.products__item {
  margin-bottom: 30px;
}
.products__item--last {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .products {
    padding: 67px 0 0;
  }
}
@media (min-width: 1150px) {
  .products {
    padding-top: 111px;
  }
  .products__list {
    display: flex;
    align-items: stretch;
    align-content: stretch;
    gap: 51px;
  }
  .products__item {
    margin-bottom: 0;
  }
}
.product__wrapper {
  padding: 27px 26px;
  box-sizing: border-box;
  border: 1px solid #ececec;
}
.product__wrapper:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.product__wrapper:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
.product__image {
  display: block;
}
.product__title {
  margin: 0;
  margin-bottom: 6px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
.product__title:hover {
  opacity: 0.6;
}
.product__title:active {
  opacity: 0.3;
}
.product__description {
  margin: 0;
  margin-bottom: 18px;
  font-size: 13px;
  line-height: 30px;
}
.product__price-wrapper {
  display: flex;
  padding-top: 22px;
  justify-content: space-between;
  border-top: 1px solid #ececec;
}
.product__price {
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: #000000;
}
.product__card {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.product__card-icon {
  fill: #000000;
  opacity: 0.2;
}
.product__card-icon:hover {
  fill: #63d1bb;
  opacity: 1;
}
.product__card-icon:active {
  fill: #63d1bb;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .product {
    display: flex;
    min-height: 334px;
    box-sizing: border-box;
  }
  .product__image {
    width: 334px;
    height: auto;
    min-height: 334px;
  }
  .product__wrapper {
    width: 100%;
    padding: 27px 34px 25px;
    display: flex;
    flex-direction: column;
  }
  .product__price-wrapper {
    margin-top: auto;
  }
}
@media (min-width: 1150px) {
  .product {
    width: 316px;
    flex-direction: column;
  }
  .product__wrapper {
    height: 100%;
  }
  .product__image {
    width: 316px;
    min-height: 0;
  }
}
.production {
  padding: 0 30px 58px;
  box-sizing: border-box;
}
.production__wrapper {
  padding: 22px 33px 33px;
  box-sizing: border-box;
  border: 1px solid #ececec;
}
.production__wrapper:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.production__wrapper:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
.production__title {
  display: block;
  margin: 0;
  margin-bottom: 26px;
  padding-left: 57px;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  box-sizing: border-box;
  background-image: url("../img/icon/icon-camera.svg");
  background-repeat: no-repeat;
  background-position: 0 50%;
}
.production__image-wrapper {
  margin-bottom: 35px;
  position: relative;
  display: block;
  cursor: pointer;
}
.production__image-wrapper::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../img/icon/icon-play.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  z-index: 1;
}
.production__image-wrapper:hover::before {
  transform: scale(1.2);
}
.production__image-wrapper:active::before {
  transform: scale(0.8);
  opacity: 0.3;
}
.production__image {
  display: block;
  margin-left: -34px;
  box-sizing: border-box;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}
.production__text {
  display: block;
  margin-top: 0;
  margin-right: -10px;
  margin-bottom: 41px;
  font-size: 13px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .production {
    padding: 53px 0 64px;
  }
  .production__wrapper {
    display: flex;
    flex-direction: column;
    padding: 50px;
    border-top: none;
  }
  .production__image-wrapper {
    order: -1;
  }
  .production__title {
    border-bottom: none;
  }
  .production__image {
    width: 668px;
    height: auto;
    margin-left: -51px;
    margin-top: -50px;
    border-top: none;
  }
}
@media (min-width: 1150px) {
  .production__wrapper {
    border: none;
    display: grid;
    grid-template-columns: 682px 288px;
    column-gap: 80px;
    padding: 0;
  }
  .production__image-wrapper {
    margin: 0;
    border: none;
    grid-column: 1 / 2;
    grid-row: 1 / 4;
  }
  .production__image {
    margin: 0;
  }
  .production__text {
    width: 288px;
  }
}
.add-cart {
  width: 320px;
  position: relative;
  margin: auto;
  padding: 28px 30px 46px;
  box-sizing: border-box;
  background-color: #ffffff;
  border: 1px solid #ececec;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
}
.add-cart__title {
  display: block;
  margin-left: 44px;
  margin-right: 40px;
  margin-top: 0;
  margin-bottom: 14px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
.add-cart__subtitle {
  display: block;
  margin: 0 49px 22px;
  font-size: 15px;
  line-height: 30px;
  text-align: center;
}
.add-cart__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-bottom: 31px;
  justify-content: space-between;
}
.size-control__mark {
  display: flex;
  width: 82px;
  height: 52px;
  box-sizing: border-box;
  border: 2px solid rgba(68, 68, 68, 0.1);
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.size-control__mark:hover {
  border-color: rgba(68, 68, 68, 0.3);
}
.size-control__mark:active {
  opacity: 1;
  border-color: #63d1bb;
}
.size-control__input:checked + .size-control__mark {
  opacity: 1;
  border-color: #63d1bb;
}
.info-slogan {
  padding: 17px 0 29px;
}
.info-slogan__wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 0 30px;
  box-sizing: border-box;
}
.info-slogan__text {
  margin: 0;
  font-size: 13px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .info-slogan {
    padding-top: 54px;
    padding-bottom: 50px;
  }
  .info-slogan__wrapper {
    width: 768px;
    padding: 0 50px;
  }
  .info-slogan__text {
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .info-slogan__wrapper {
    width: 1150px;
    padding-left: 174px;
    padding-right: 174px;
  }
}
.title-legend {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #63d1bb;
  text-transform: uppercase;
}
