.type-control {
  position: relative;
  min-height: 42px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.type-control__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-sizing: border-box;
  border: 2px solid #444444;
}
.type-control__label {
  margin-left: 60px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
}
.type-control__input:checked + .type-control__mark::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #63d1bb;
}
.type-control__input:hover + .type-control__mark {
  border-color: #63d1bb;
}
.type-control__input:active + .type-control__mark {
  border-color: rgba(0, 0, 0, 0.3);
}
.color-control {
  position: relative;
  min-height: 38px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.color-control__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 38px;
  height: 38px;
  box-sizing: border-box;
  border: 2px solid #444444;
}
.color-control__label {
  margin-left: 60px;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
}
.color-control__input:checked + .color-control__mark::before {
  position: absolute;
  content: "";
  width: 21px;
  height: 10px;
  top: 50%;
  left: 50%;
  box-sizing: border-box;
  border-left: 2px solid #000000;
  border-bottom: 2px solid #000000;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.color-control__input:hover + .color-control__mark {
  border-color: #63d1bb;
}
.color-control__input:active + .color-control__mark {
  border-color: rgba(0, 0, 0, 0.3);
}
.fieldset-order {
  margin: 0 auto;
  width: 320px;
  padding: 0 30px;
  border: none;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .fieldset-order {
    width: 768px;
    padding: 50px;
  }
}
@media (min-width: 1150px) {
  .fieldset-order {
    width: 1150px;
  }
}
